*, *::before, *::after{
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box;
}

:root{
  --primary-color: #90C2C4;
  --secondary-color: #369093;
  --tertiary-color: #CA4838;
  --success-color: #A1EDA5;
  --success-dark-color: #75CF7A;
  --try-again-color: #FF8847;
  --disabled1-color: #909494;
  --disabled2-color: #B4B4B4;
  --background-color: #F7F7F7;

  /* ===== BOEKENKAST ===== */
  --book-color-red: linear-gradient(180deg, #FF6363 0%, #CC2A2A 100%);
  --book-color-yellow: linear-gradient(180deg, #FFF170 0%, #AE8F3E 100%);
  --book-color-green: linear-gradient(180deg, #4EE06E 0%, #34A44C 100%);
  --book-color-blue: linear-gradient(180deg, #8CF1FF 0%, #52ACB9 100%);
  --book-color-purple: linear-gradient(180deg, #EB70FF 0%, #AC3EAE 100%);
  --cancel-color-primary: #ffb3b3;
  --cancel-color-secondary: #fa5353;

  /* ===== GENRES ===== */
  --avontuur: #cb7cff;
  --humor: #ff8847;
  --fantasie: #ffeb3e;
  --sprookjes: #335d2d;
  --dieren-en-natuur: #a1eda5;
  --spanning: #fc6c6c;
  --vriendschap-en-verliefd: #ba135d;
  --alle-genres: #90c2c4;
  --mijn-favorieten: #9d9aff;

  /* ==== FONTS ==== */
  --fnt-roboto: 'Roboto', sans-serif;
  --fnt-assistant: 'Assistant', sans-serif;
}

html{
  font-size: 62.5%;
}

body{
  background-color: #F7F7F7;
