  background-color: var(--primary-color);
  border: 4px solid #369093;
  border: 4px solid var(--secondary-color);
  box-shadow: 2px 4px #888888;
}

.u-button:hover{
  cursor: pointer;
  background-color: #369093;
  background-color: var(--secondary-color);
  border: 4px solid #90C2C4;
  border: 4px solid var(--primary-color);
}

.u-button:active{
  top:1rem;
  background-color:#90C2C4;
  box-shadow:inset 0 1px 0 #FFE5C4, inset 0 -.2px 0 #915100;
}

.u-button--close{
  background: #90C2C4;
  background: var(--primary-color);
  border: .5rem solid #369093;
  border: .5rem solid var(--secondary-color);
  border-radius: 50%;
  top: 0;
  right: 0;
  float: right;
  position: absolute;
  padding: 1rem 1.8rem;
  font-size: 3rem;
}

.u-button--close:hover{
  background: #369093;
  background: var(--secondary-color);
  border: .5rem solid #90C2C4;
  border: .5rem solid var(--primary-color);
  cursor: pointer;
}

